#feedback {
  padding: 30px 0;
}
#feedback .content {
  box-shadow: 0px 0px 10px 0px rgba(221, 221, 221, 0.5);
  background-color: #fff;
  min-height: 84vh;
  padding: 40px 12% 40px 52px;
  box-sizing: border-box;
  padding-bottom: 80px;
  position: relative;
}
#feedback .content .head {
  background: #eceff4;
  padding: 8px 28px;
  box-sizing: border-box;
}
#feedback .content .head .ant-badge {
  margin-right: 20px;
}
#feedback .content .head .ant-badge .feedBack {
  padding: 8px 18px;
}
#feedback .content .head .feedBack {
  padding: 4px 18px;
  display: inline-block;
  background: #ffffff;
  border: 1px solid #c3c5ce;
  color: #333;
  font-size: 16px;
  cursor: pointer;
}
#feedback .content .head .statusActive {
  background: #f06e65;
  border: 1px solid #ed5a50;
  color: #fff;
}
#feedback .content .listBox {
  padding: 10px 0;
}
#feedback .content .listBox .list {
  margin-top: 20px;
  padding: 20px 28px 16px 28px;
  box-sizing: border-box;
  padding-right: 10%;
  position: relative;
  background-color: #F9FBFF;
  border-radius: 4px;
}
#feedback .content .listBox .list .handle {
  background: #f1948e;
  border-radius: 6px;
  color: #fff;
  font-size: 14px;
  position: absolute;
  bottom: 10px;
  padding: 6px 20px;
  right: 4.5%;
  cursor: pointer;
}
#feedback .content .listBox .list .title {
  color: #1E2226;
  font-size: 20px;
  letter-spacing: 1px;
  max-width: 60%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#feedback .content .listBox .list .type {
  margin-top: 12px;
  color: #666666;
  font-size: 16px;
}
#feedback .content .listBox .list .time {
  color: #999999;
  font-size: 16px;
  margin-top: 2.6%;
}
#feedback .content .listBox .list .stepImg {
  width: 60px;
  border-radius: 50%;
  position: absolute;
  top: 8%;
  right: 2%;
}
#feedback .content .pagination {
  display: flex;
  padding: 10px 0;
  align-content: center;
  justify-content: center;
  position: absolute;
  bottom: 20px;
  width: 100%;
  left: 0;
}
#feedback .content .pagination span {
  display: inline-block;
  padding: 8px;
  cursor: pointer;
}
.info .ant-modal-body {
  padding: 30px;
  border-radius: 8px;
}
.info .ant-modal-body .flexBox {
  display: flex;
}
.info .ant-modal-body .flexBox .flex {
  flex: 1;
}
.info .ant-modal-body .info_content {
  border-bottom: 2px solid #dedede;
  padding-bottom: 3%;
}
.info .ant-modal-body .info_content .flex_l {
  padding-right: 10px;
}
.info .ant-modal-body .info_content .flex_l .headImg {
  width: 46px;
  height: 46px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
}
.info .ant-modal-body .info_content .flex_l .headImg img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.info .ant-modal-body .info_content .flex {
  flex: 1;
}
.info .ant-modal-body .info_content .info_m {
  border-radius: 4px;
  border: 1px solid #d8d8d8;
  padding: 14px 24px;
  box-sizing: border-box;
}
.info .ant-modal-body .info_content .info_m .info_title {
  color: #666666;
  font-size: 18px;
}
.info .ant-modal-body .info_content .info_m .imgList {
  padding-top: 15px;
}
.info .ant-modal-body .info_content .info_m .imgList img {
  width: 20%;
  margin-right: 20px;
}
.info .ant-modal-body .steps {
  padding-top: 2.67%;
}
.info .ant-modal-body .steps .step_title {
  color: #333;
  font-size: 18px;
}
.info .ant-modal-body .steps .step_list {
  padding-top: 20px;
}
.info .ant-modal-body .steps .step_list .step .step_time {
  padding-right: 10px;
  padding-bottom: 38px;
}
.info .ant-modal-body .steps .step_list .step .step_time p {
  margin: 0;
  color: #999999;
  font-size: 12px;
  text-align: right;
}
.info .ant-modal-body .steps .step_list .step .step_r {
  padding-left: 10px;
  color: #666;
}
.info .ant-modal-body .steps .step_list .step .step_r span {
  color: #999;
}
.info .ant-modal-body .steps .step_list .step .step_m {
  position: relative;
  padding-top: 0.19%;
}
.info .ant-modal-body .steps .step_list .step .step_m .step_status {
  width: 20px;
  height: 20px;
  padding: 2px;
  border-radius: 50%;
  box-sizing: border-box;
  z-index: 99;
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0);
}
.info .ant-modal-body .steps .step_list .step .step_m .step_status div {
  width: 14px;
  height: 14px;
  background-color: #FF867E;
  border-radius: 50%;
}
.info .ant-modal-body .steps .step_list .step .step_m .status1 {
  border: 1px solid #FF6A6A;
  background-color: #fff;
}
.info .ant-modal-body .steps .step_list .step .step_m .noStatus div {
  background-color: #CBCBCB;
}
.info .ant-modal-body .steps .step_list .step .step_m::before {
  content: '';
  position: absolute;
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
  width: 1px;
  height: 100%;
  background-color: #D2D2D2;
}
.info .ant-modal-body .steps .step_list .step:last-child .step_m::before {
  content: '';
  position: absolute;
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
  width: 1px;
  height: 100%;
  background-color: #D2D2D2;
  opacity: 0;
}
.info .ant-modal-body .btn {
  padding-top: 60px;
  text-align: right;
}
.info .ant-modal-body .btn span {
  display: inline-block;
  text-align: center;
  cursor: pointer;
  width: 112px;
  height: 44px;
  line-height: 42px;
  font-size: 18px;
  border-radius: 2px;
  border: 1px solid #979797;
  margin-left: 30px;
}
.info .ant-modal-body .btn .active {
  background: #f06e65;
  border-radius: 2px;
  border: 1px solid #ee6258;
  color: #fff;
}
.reply .ant-modal-body {
  padding: 30px 80px;
  box-sizing: border-box;
  border-radius: 8px;
}
.reply .flexBox {
  color: #333333;
  display: flex;
  align-items: center;
}
.reply .flexBox .flex {
  flex: 1;
}
.reply .flexBox .flex .flex {
  padding-left: 10%;
  box-sizing: border-box;
}
.reply .flexBox .flex span {
  height: 21px;
  display: inline-block;
  line-height: 19px;
  cursor: pointer;
  color: #333333;
}
.reply .flexBox .flex span span {
  border: 1px solid #979797;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  text-align: center;
  display: inline-block;
  margin-right: 8px;
  vertical-align: middle;
  margin-top: -3.8%;
}
.reply .flexBox .flex span .active {
  background: #f1948e;
  border-color: #f1948e;
  border: none;
  line-height: 21px;
  margin-top: -6.8%;
}
.reply .replyInfo {
  padding-top: 40px;
}
.reply textarea {
  resize: none;
  font-size: 14px;
}
.reply .btn {
  padding-top: 60px;
  text-align: center;
}
.reply .btn span {
  display: inline-block;
  cursor: pointer;
  width: 112px;
  height: 48px;
  line-height: 48px;
  font-size: 20px;
  border-radius: 2px;
  border: 1px solid #979797;
  margin-left: 30px;
}
.reply .btn .active {
  background: #f06e65;
  border-radius: 2px;
  border: 1px solid #ee6258;
  color: #fff;
}
